.button {
  display: inline-flex;
  width: 100%;

  align-items: center;
  justify-content: center;
  text-decoration: none;

  margin-bottom: 20px;
  padding: 20px 16px;
  font-size: 15px;
  font-weight: 700;
  line-height: 16.73px;

  border: none;
  border-radius: 10px;
  color: var(--white);
  background: var(--accent);
  cursor: pointer;

  transition: var(--defaultTransition);

  &:hover {
    background: var(--hover);
  }
}
