.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  width: 328px;
  margin-bottom: 20px;
  color: var(--gray);
}

.text {
  display: block;
  text-decoration: none;
  font-size: 15px;
  color: var(--accent);
  margin-bottom: 20px;
  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 328px;
  height: 57px;
  font-size: 15px;
  color: var(--accent);
  text-decoration: none;
  border: 1px solid var(--accent);
  border-radius: 10px;
  margin-bottom: 50px;
  transition: var(--defaultTransition);

  &:hover {
    background-color: var(--hover);
    border: 1px solid var(--hover);
    color: var(--white);
  }
}
