.input {
  background-color: rgba(255, 255, 255, 0.15);
  color: #585858;
  padding-left: 15px;
  height: 57px;
  width: 100%;
  border: 1px solid #c7c7c7;
  border-radius: 5px;
  cursor: pointer;

  &::placeholder {
    color: #585858;
  }

  &:hover {
    background-color: transparent;
  }

  &:focus {
    background-color: rgba(255, 255, 255, 0.15);
  }
}
